import { supabase } from '@/services/supabase'
import { validateSupabaseConfig, logConfigReport } from '@/utils/configValidator'
import { checkDatabaseStatus, logMigrationStatus } from '@/utils/migrationRunner'

/**
 * 
 */
export async function initializeApp(): Promise<{
  success: boolean
  message: string
  details: {
    configValid: boolean
    databaseReady: boolean
    authReady: boolean
  }
}> {
  console.log(' ...')
  
  const startTime = Date.now()
  const results = {
    configValid: false,
    databaseReady: false,
    authReady: false
  }

  try {
    // 1. 
    console.log(' Supabase...')
    const configResult = await validateSupabaseConfig()
    results.configValid = configResult.isValid
    
    if (!results.configValid) {
      console.warn(' Supabase')
      console.log('.env')
    } else {
      console.log(' Supabase')
    }

    // 2. 
    console.log(' ...')
    const dbStatus = await checkDatabaseStatus()
    results.databaseReady = dbStatus.tablesExist
    
    if (!results.databaseReady) {
      console.warn(' ')
      console.log(' /supabase-manager ')
    } else {
      console.log(' ')
    }

    // 3. 
    console.log(' ...')
    const authResult = await initializeAuth()
    results.authReady = authResult.success
    
    if (!results.authReady) {
      console.warn(' ')
    } else {
      console.log(' ')
    }

    // 4. 
    setupAuthListeners()

    const endTime = Date.now()
    const duration = endTime - startTime

    const success = results.configValid && results.databaseReady && results.authReady
    
    if (success) {
      console.log(`  (${duration}ms)`)
      console.log(' :')
      console.log('  - :  ')
      console.log('  - :  ')
      console.log('  - :  ')
    } else {
      console.warn(`  (${duration}ms)`)
      console.log(' :')
      console.log(`  - : ${results.configValid ? '' : ''}`)
      console.log(`  - : ${results.databaseReady ? '' : ''}`)
      console.log(`  - : ${results.authReady ? '' : ''}`)
    }

    return {
      success,
      message: success ? '' : '',
      details: results
    }

  } catch (error) {
    console.error(' :', error)
    
    return {
      success: false,
      message: ': ' + (error instanceof Error ? error.message : ''),
      details: results
    }
  }
}

/**
 * 
 */
async function initializeAuth(): Promise<{ success: boolean; session?: any }> {
  try {
    // 
    const { data: { session }, error } = await supabase.auth.getSession()
    
    if (error) {
      console.error(' :', error)
      return { success: false }
    }

    if (session) {
      console.log(' ')
      console.log(' ID:', session.user.id)
      console.log(' :', session.user.email)
    } else {
      console.log(' ')
    }

    return { success: true, session }

  } catch (error) {
    console.error(' :', error)
    return { success: false }
  }
}

/**
 * 
 */
function setupAuthListeners(): void {
  // 
  supabase.auth.onAuthStateChange((event, session) => {
    console.log(' :', event)
    
    switch (event) {
      case 'SIGNED_IN':
        console.log(' ')
        console.log(' ID:', session?.user.id)
        console.log(' :', session?.user.email)
        
        // 
        window.dispatchEvent(new CustomEvent('auth-state-changed', {
          detail: { event, session }
        }))
        break
        
      case 'SIGNED_OUT':
        console.log(' ')
        
        // 
        window.dispatchEvent(new CustomEvent('auth-state-changed', {
          detail: { event, session: null }
        }))
        break
        
      case 'TOKEN_REFRESHED':
        console.log(' ')
        break
        
      case 'USER_UPDATED':
        console.log(' ')
        break
    }
  })

  // 
  window.addEventListener('online', () => {
    console.log(' ')
    
    // 
    setTimeout(() => {
      validateSupabaseConfig().catch(console.error)
    }, 1000)
  })

  window.addEventListener('offline', () => {
    console.warn(' ')
  })
}

/**
 * 
 */
export async function initializeDevelopmentMode(): Promise<void> {
  if (!import.meta.env.DEV) return

  console.log(' ...')
  
  try {
    // 
    console.log(' :')
    console.log('  - :', import.meta.env.MODE)
    console.log('  - URL:', import.meta.env.BASE_URL)
    console.log('  - Supabase URL:', import.meta.env.VITE_SUPABASE_URL || '')
    console.log('  - Anon Key:', import.meta.env.VITE_SUPABASE_ANON_KEY ? '' : '')

    // 
    await logConfigReport()
    
    // 
    await logMigrationStatus()

    // 
    if (typeof window !== 'undefined') {
      // @ts-ignore
      window.__FORUM_DEBUG__ = {
        supabase,
        checkConnection: () => validateSupabaseConfig(),
        checkDatabase: () => checkDatabaseStatus(),
        getSession: () => supabase.auth.getSession(),
        signOut: () => supabase.auth.signOut(),
        runMigrations: () => import('@/utils/migrationRunner').then(m => m.runMigrations())
      }
      
      console.log('  window.__FORUM_DEBUG__')
    }

    console.log(' ')

  } catch (error) {
    console.error(' :', error)
  }
}

/**
 * 
 */
export async function initializeProductionMode(): Promise<void> {
  if (!import.meta.env.PROD) return

  console.log(' ...')
  
  try {
    // 
    if ('performance' in window) {
      performance.mark('app-start')
    }

    // 
    const criticalResources = [
      '/src/services/supabase.ts',
      '/src/stores/auth.ts',
      '/src/router/index.ts'
    ]

    console.log(' ...')
    
    // 
    //  Service Worker 

    console.log(' ')

  } catch (error) {
    console.error(' :', error)
  }
}

/**
 * 
 */
export function initializeErrorHandling(): void {
  // 
  window.addEventListener('error', (event) => {
    console.error(' :', event.error)
    
    // 
    if (import.meta.env.PROD) {
      // 
      console.log(' ...')
    }
  })

  // Promise 
  window.addEventListener('unhandledrejection', (event) => {
    console.error(' Promise:', event.reason)
    
    // 
    if (import.meta.env.PROD) {
      console.log(' Promise...')
    }
  })

  // Vue 
  if (typeof window !== 'undefined') {
    // @ts-ignore
    window.__VUE_ERROR_HANDLER__ = (err, instance, info) => {
      console.error(' Vue:', err)
      console.error(':', instance)
      console.error(':', info)
    }
  }

  console.log(' ')
}

/**
 * 
 */
export async function startup(): Promise<void> {
  console.log(' ...')
  
  try {
    // 1. 
    initializeErrorHandling()

    // 2. 
    if (import.meta.env.DEV) {
      await initializeDevelopmentMode()
    } else if (import.meta.env.PROD) {
      await initializeProductionMode()
    }

    // 3. 
    const initResult = await initializeApp()
    
    // 4. 
    window.dispatchEvent(new CustomEvent('app-startup-complete', {
      detail: initResult
    }))

    console.log(' ')

  } catch (error) {
    console.error(' :', error)
    
    // 
    window.dispatchEvent(new CustomEvent('app-startup-failed', {
      detail: { error }
    }))
  }
}

// 
if (typeof window !== 'undefined') {
  // DOM
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', () => {
      setTimeout(startup, 100)
    })
  } else {
    setTimeout(startup, 100)
  }
}