import { supabase } from '@/services/supabase'

/**
 * 
 */
export interface MigrationResult {
  success: boolean
  message: string
  details?: string
  executedMigrations?: string[]
  errors?: MigrationError[]
}

export interface MigrationError {
  migration: string
  error: string
  sql?: string
}

/**
 * 
 */
export async function runMigrations(): Promise<MigrationResult> {
  console.log(' ...')
  
  const executedMigrations: string[] = []
  const errors: MigrationError[] = []

  try {
    // 
    const migrationFiles = await getMigrationFiles()
    
    if (migrationFiles.length === 0) {
      return {
        success: false,
        message: '',
        details: 'supabase/migrationsSQL'
      }
    }

    console.log(`  ${migrationFiles.length} `)

    // 
    migrationFiles.sort()

    for (const migrationFile of migrationFiles) {
      try {
        console.log(` : ${migrationFile}`)
        
        const sqlContent = await getMigrationSQL(migrationFile)
        if (!sqlContent) {
          throw new Error(`: ${migrationFile}`)
        }

        // SQL
        const { error } = await supabase.rpc('exec_sql', { sql: sqlContent })
        
        if (error) {
          // exec_sqlSQL
          if (error.message.includes('function "exec_sql" does not exist')) {
            await executeRawSQL(sqlContent)
          } else {
            throw error
          }
        }

        executedMigrations.push(migrationFile)
        console.log(` : ${migrationFile}`)
        
      } catch (error: any) {
        console.error(` : ${migrationFile}`, error)
        errors.push({
          migration: migrationFile,
          error: error.message,
          sql: await getMigrationSQL(migrationFile)
        })
      }
    }

    if (errors.length > 0) {
      return {
        success: false,
        message: ` ${errors.length} `,
        executedMigrations,
        errors
      }
    }

    return {
      success: true,
      message: ` (${executedMigrations.length} )`,
      executedMigrations,
      details: 'RLS'
    }

  } catch (error: any) {
    console.error(' :', error)
    return {
      success: false,
      message: '',
      details: error.message,
      errors
    }
  }
}

/**
 * 
 */
async function getMigrationFiles(): Promise<string[]> {
  // 
  // API
  
  // 
  return ['001_create_tables.sql']
}

/**
 * 
 */
async function getMigrationSQL(filename: string): Promise<string | null> {
  try {
    // API
    // SQL
    
    if (filename === '001_create_tables.sql') {
      return `
        -- 
        -- 001_create_tables.sql

        -- 1.  (profiles)
        CREATE TABLE IF NOT EXISTS profiles (
            id UUID PRIMARY KEY REFERENCES auth.users(id) ON DELETE CASCADE,
            username TEXT UNIQUE NOT NULL,
            avatar_url TEXT,
            level INTEGER DEFAULT 1 CHECK (level >= 1),
            experience_points INTEGER DEFAULT 0 CHECK (experience_points >= 0),
            created_at TIMESTAMPTZ DEFAULT NOW(),
            updated_at TIMESTAMPTZ DEFAULT NOW()
        );

        -- 2.  (posts)
        CREATE TABLE IF NOT EXISTS posts (
            id UUID PRIMARY KEY DEFAULT gen_random_uuid(),
            user_id UUID NOT NULL REFERENCES profiles(id) ON DELETE CASCADE,
            title TEXT NOT NULL CHECK (char_length(title) >= 1 AND char_length(title) <= 200),
            content TEXT NOT NULL CHECK (char_length(content) >= 1),
            tags TEXT[] DEFAULT '{}',
            like_count INTEGER DEFAULT 0 CHECK (like_count >= 0),
            comment_count INTEGER DEFAULT 0 CHECK (comment_count >= 0),
            view_count INTEGER DEFAULT 0 CHECK (view_count >= 0),
            is_pinned BOOLEAN DEFAULT FALSE,
            created_at TIMESTAMPTZ DEFAULT NOW(),
            updated_at TIMESTAMPTZ DEFAULT NOW()
        );

        -- 3.  (comments)
        CREATE TABLE IF NOT EXISTS comments (
            id UUID PRIMARY KEY DEFAULT gen_random_uuid(),
            post_id UUID NOT NULL REFERENCES posts(id) ON DELETE CASCADE,
            user_id UUID NOT NULL REFERENCES profiles(id) ON DELETE CASCADE,
            content TEXT NOT NULL CHECK (char_length(content) >= 1 AND char_length(content) <= 1000),
            parent_id UUID REFERENCES comments(id) ON DELETE CASCADE,
            like_count INTEGER DEFAULT 0 CHECK (like_count >= 0),
            is_pinned BOOLEAN DEFAULT FALSE,
            created_at TIMESTAMPTZ DEFAULT NOW(),
            updated_at TIMESTAMPTZ DEFAULT NOW()
        );

        -- 4.  (interactions)
        CREATE TABLE IF NOT EXISTS interactions (
            id UUID PRIMARY KEY DEFAULT gen_random_uuid(),
            user_id UUID NOT NULL REFERENCES profiles(id) ON DELETE CASCADE,
            target_type TEXT NOT NULL CHECK (target_type IN ('post', 'comment')),
            target_id UUID NOT NULL,
            interaction_type TEXT NOT NULL CHECK (interaction_type IN ('like', 'bookmark', 'share')),
            created_at TIMESTAMPTZ DEFAULT NOW(),
            
            -- 
            UNIQUE(user_id, target_type, target_id, interaction_type)
        );

        -- 
        CREATE INDEX IF NOT EXISTS idx_profiles_username ON profiles(username);
        CREATE INDEX IF NOT EXISTS idx_profiles_level ON profiles(level);
        CREATE INDEX IF NOT EXISTS idx_profiles_experience ON profiles(experience_points);
        CREATE INDEX IF NOT EXISTS idx_posts_user_id ON posts(user_id);
        CREATE INDEX IF NOT EXISTS idx_posts_created_at ON posts(created_at DESC);
        CREATE INDEX IF NOT EXISTS idx_posts_like_count ON posts(like_count DESC);
        CREATE INDEX IF NOT EXISTS idx_posts_comment_count ON posts(comment_count DESC);
        CREATE INDEX IF NOT EXISTS idx_posts_is_pinned ON posts(is_pinned);
        CREATE INDEX IF NOT EXISTS idx_posts_tags ON posts USING GIN(tags);
        CREATE INDEX IF NOT EXISTS idx_comments_post_id ON comments(post_id);
        CREATE INDEX IF NOT EXISTS idx_comments_user_id ON comments(user_id);
        CREATE INDEX IF NOT EXISTS idx_comments_parent_id ON comments(parent_id);
        CREATE INDEX IF NOT EXISTS idx_comments_created_at ON comments(created_at DESC);
        CREATE INDEX IF NOT EXISTS idx_comments_like_count ON comments(like_count DESC);
        CREATE INDEX IF NOT EXISTS idx_comments_is_pinned ON comments(is_pinned);
        CREATE INDEX IF NOT EXISTS idx_interactions_user_id ON interactions(user_id);
        CREATE INDEX IF NOT EXISTS idx_interactions_target ON interactions(target_type, target_id);
        CREATE INDEX IF NOT EXISTS idx_interactions_type ON interactions(interaction_type);
        CREATE INDEX IF NOT EXISTS idx_interactions_created_at ON interactions(created_at DESC);

        --  (RLS)
        ALTER TABLE profiles ENABLE ROW LEVEL SECURITY;
        ALTER TABLE posts ENABLE ROW LEVEL SECURITY;
        ALTER TABLE comments ENABLE ROW LEVEL SECURITY;
        ALTER TABLE interactions ENABLE ROW LEVEL SECURITY;

        -- RLS
        
        -- profiles
        CREATE POLICY "" ON profiles
            FOR SELECT USING (true);

        CREATE POLICY "" ON profiles
            FOR INSERT WITH CHECK (auth.uid() = id);

        CREATE POLICY "" ON profiles
            FOR UPDATE USING (auth.uid() = id);

        -- posts
        CREATE POLICY "" ON posts
            FOR SELECT USING (true);

        CREATE POLICY "" ON posts
            FOR INSERT WITH CHECK (auth.uid() = user_id);

        CREATE POLICY "" ON posts
            FOR UPDATE USING (auth.uid() = user_id);

        CREATE POLICY "" ON posts
            FOR DELETE USING (auth.uid() = user_id);

        -- comments
        CREATE POLICY "" ON comments
            FOR SELECT USING (true);

        CREATE POLICY "" ON comments
            FOR INSERT WITH CHECK (auth.uid() = user_id);

        CREATE POLICY "" ON comments
            FOR UPDATE USING (auth.uid() = user_id);

        CREATE POLICY "" ON comments
            FOR DELETE USING (auth.uid() = user_id);

        -- interactions
        CREATE POLICY "" ON interactions
            FOR SELECT USING (auth.uid() = user_id);

        CREATE POLICY "" ON interactions
            FOR INSERT WITH CHECK (auth.uid() = user_id);

        CREATE POLICY "" ON interactions
            FOR DELETE USING (auth.uid() = user_id);
      `
    }
    
    return null
  } catch (error) {
    console.error(` : ${filename}`, error)
    return null
  }
}

/**
 * SQL
 */
async function executeRawSQL(sql: string): Promise<void> {
  // SQL
  const statements = sql.split(';').filter(stmt => stmt.trim())
  
  for (const statement of statements) {
    if (statement.trim()) {
      const { error } = await supabase.rpc('exec_sql', { sql: statement + ';' })
      
      if (error) {
        // SQL API
        throw new Error(`SQL: ${error.message}`)
      }
    }
  }
}

/**
 * 
 */
export async function checkDatabaseStatus(): Promise<{
  tablesExist: boolean
  tables: { name: string; exists: boolean; rowCount?: number }[]
}> {
  const tables = ['profiles', 'posts', 'comments', 'interactions']
  const tableStatus = []

  for (const tableName of tables) {
    try {
      const { data, error } = await supabase
        .from(tableName)
        .select('*', { count: 'exact', head: true })
      
      tableStatus.push({
        name: tableName,
        exists: !error,
        rowCount: data ? data.length : 0
      })
    } catch (error) {
      tableStatus.push({
        name: tableName,
        exists: false,
        rowCount: 0
      })
    }
  }

  const tablesExist = tableStatus.every(table => table.exists)

  return {
    tablesExist,
    tables: tableStatus
  }
}

/**
 * 
 */
export async function logMigrationStatus(): Promise<void> {
  console.log(' ...')
  
  const dbStatus = await checkDatabaseStatus()
  
  console.log(' :')
  dbStatus.tables.forEach(table => {
    const status = table.exists ? '' : ''
    console.log(`  ${status} ${table.name}: ${table.exists ? '' : ''}`)
  })
  
  if (!dbStatus.tablesExist) {
    console.log(' ')
    
    const runMigration = confirm('')
    if (runMigration) {
      await runMigrations()
    }
  } else {
    console.log(' ')
  }
}

// 
if (import.meta.env.DEV) {
  setTimeout(() => {
    logMigrationStatus()
  }, 2000)
}