import { createRouter, createWebHistory } from 'vue-router'
import type { RouteRecordRaw } from 'vue-router'

const routes: RouteRecordRaw[] = [
  {
    path: '/',
    name: 'Home',
    component: () => import('@/views/HomeView.vue'),
    meta: { title: ' - ' }
  },
  {
    path: '/connection-test',
    name: 'ConnectionTest',
    component: () => import('@/views/ConnectionTestView.vue'),
    meta: { title: '' }
  },
  {
    path: '/supabase-manager',
    name: 'SupabaseManager',
    component: () => import('@/views/SupabaseManagerView.vue'),
    meta: { title: 'Supabase' }
  },
  {
    path: '/post/:id',
    name: 'PostDetail',
    component: () => import('@/views/PostDetailView.vue'),
    meta: { title: '' }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/LoginView.vue'),
    meta: { title: '', requiresGuest: true }
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('@/views/RegisterView.vue'),
    meta: { title: '', requiresGuest: true }
  },
  {
    path: '/profile',
    name: 'Profile',
    component: () => import('@/views/ProfileView.vue'),
    meta: { title: '', requiresAuth: true }
  },
  {
    path: '/profile/:id',
    name: 'UserProfile',
    component: () => import('@/views/ProfileView.vue'),
    meta: { title: '' }
  },
  {
    path: '/messages',
    name: 'MessageCenter',
    component: () => import('@/views/MessageCenterView.vue'),
    meta: { title: '', requiresAuth: true }
  },
  {
    path: '/search',
    name: 'Search',
    component: () => import('@/views/SearchView.vue'),
    meta: { title: '' }
  },
  {
    path: '/bookmarks',
    name: 'Bookmarks',
    component: () => import('@/views/BookmarksView.vue'),
    meta: { title: '', requiresAuth: true }
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'NotFound',
    component: () => import('@/views/NotFoundView.vue'),
    meta: { title: '' }
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

//  - 
router.beforeEach(async (to, from, next) => {
  try {
    console.log('', from.path, '', to.path)
    
    // 
    if (to.meta.requiresAuth) {
      console.log(':', to.path)
      
      // 
      const supabaseUrl = import.meta.env.VITE_SUPABASE_URL
      console.log('Supabase URL:', supabaseUrl)
      
      if (!supabaseUrl || supabaseUrl.includes('default.supabase.co')) {
        console.log('')
        next()
        return
      }
      
      // 
      try {
        const { useAuthStore } = await import('@/stores/auth')
        const authStore = useAuthStore()
        
        console.log('Auth store:', {
          user: !!authStore.user,
          profile: !!authStore.profile,
          isAuthenticated: authStore.isAuthenticated
        })
        
        // store
        if (!authStore.user) {
          console.log('Auth store...')
          await authStore.initializeAuth()
          console.log(':', {
            user: !!authStore.user,
            profile: !!authStore.profile,
            isAuthenticated: authStore.isAuthenticated
          })
        }
        
        // 
        if (authStore.isAuthenticated) {
          console.log('')
          next()
          return
        } else {
          console.log('')
          next('/login')
          return
        }
      } catch (storeError) {
        console.error('Auth store:', storeError)
        
        // 
        if (storeError.message && storeError.message.includes('Failed to fetch dynamically imported module')) {
          console.warn('')
          next()
          return
        }
        
        console.log('')
        next('/login')
        return
      }
    }
    
    // 
    if (to.meta.requiresGuest) {
      try {
        const { useAuthStore } = await import('@/stores/auth')
        const authStore = useAuthStore()
        
        if (authStore.isAuthenticated) {
          next('/')
          return
        }
      } catch (storeError) {
        // store
      }
    }
    
    console.log(':', to.path)
    next()
  } catch (error) {
    console.error(':', error)
    // 
    next()
  }
})

// 
router.onError((error) => {
  console.error(':', error)
  
  // 
  if (error.message.includes('Failed to fetch dynamically imported module')) {
    console.warn('')
    
    // 
    const isAuthError = error.message.includes('auth') || 
                       error.message.includes('stores/auth') ||
                       error.message.includes('ProfileView')
    
    if (isAuthError) {
      console.log('')
      return
    }
    
    // 
    setTimeout(() => {
      // 
      const lastReload = sessionStorage.getItem('lastRouteErrorReload')
      const now = Date.now()
      
      if (!lastReload || now - parseInt(lastReload) > 10000) { // 10
        sessionStorage.setItem('lastRouteErrorReload', now.toString())
        console.log('')
        window.location.reload()
      } else {
        console.warn('')
      }
    }, 1000)
  }
})

export default router