import { supabase } from '@/services/supabase'

/**
 * Supabase
 */
export interface ConfigValidationResult {
  isValid: boolean
  issues: ConfigIssue[]
  recommendations: string[]
}

export interface ConfigIssue {
  type: 'error' | 'warning' | 'info'
  message: string
  field?: string
  fix?: string
}

/**
 * Supabase
 */
export async function validateSupabaseConfig(): Promise<ConfigValidationResult> {
  const issues: ConfigIssue[] = []
  const recommendations: string[] = []

  // 
  const supabaseUrl = import.meta.env.VITE_SUPABASE_URL
  const supabaseKey = import.meta.env.VITE_SUPABASE_ANON_KEY

  // 1. URL
  if (!supabaseUrl) {
    issues.push({
      type: 'error',
      message: 'VITE_SUPABASE_URL',
      field: 'VITE_SUPABASE_URL',
      fix: '.envSupabaseURL'
    })
  } else if (supabaseUrl.includes('default.supabase.co')) {
    issues.push({
      type: 'warning',
      message: 'Supabase URL',
      field: 'VITE_SUPABASE_URL',
      fix: 'SupabaseURL'
    })
  } else if (!supabaseUrl.startsWith('https://')) {
    issues.push({
      type: 'error',
      message: 'Supabase URLhttps://',
      field: 'VITE_SUPABASE_URL',
      fix: 'URLhttps://your-project-ref.supabase.co'
    })
  }

  // 2. Anon Key
  if (!supabaseKey) {
    issues.push({
      type: 'error',
      message: 'VITE_SUPABASE_ANON_KEY',
      field: 'VITE_SUPABASE_ANON_KEY',
      fix: '.envSupabase'
    })
  } else if (supabaseKey.includes('default')) {
    issues.push({
      type: 'warning',
      message: 'Anon Key',
      field: 'VITE_SUPABASE_ANON_KEY',
      fix: 'Supabase'
    })
  }

  // 3. 
  if (issues.filter(issue => issue.type === 'error').length === 0) {
    try {
      console.log(' Supabase...')
      
      // 
      const { data: authData, error: authError } = await supabase.auth.getSession()
      
      if (authError) {
        issues.push({
          type: 'error',
          message: `: ${authError.message}`,
          field: 'auth',
          fix: 'Supabase'
        })
      } else {
        console.log(' ')
      }

      // 
      const tables = ['profiles', 'posts', 'comments', 'interactions']
      const tableResults = []

      for (const tableName of tables) {
        try {
          const { data, error } = await supabase
            .from(tableName)
            .select('count')
            .limit(1)
          
          if (error) {
            if (error.code === 'PGRST301') {
              issues.push({
                type: 'error',
                message: `${tableName}`,
                field: tableName,
                fix: ''
              })
            } else {
              issues.push({
                type: 'warning',
                message: `${tableName}: ${error.message}`,
                field: tableName
              })
            }
          } else {
            tableResults.push({ table: tableName, accessible: true })
          }
        } catch (error: any) {
          issues.push({
            type: 'error',
            message: `${tableName}: ${error.message}`,
            field: tableName
          })
        }
      }

      // 
      if (tableResults.length === tables.length) {
        recommendations.push(' ')
      }

      // 
      if (authData.session) {
        recommendations.push(' ')
      } else {
        recommendations.push(' /')
      }

    } catch (error: any) {
      issues.push({
        type: 'error',
        message: `: ${error.message}`,
        fix: 'Supabase'
      })
    }
  }

  // 4. 
  if (issues.length === 0) {
    recommendations.push(' ')
  } else {
    const errorCount = issues.filter(issue => issue.type === 'error').length
    const warningCount = issues.filter(issue => issue.type === 'warning').length
    
    if (errorCount > 0) {
      recommendations.push(` ${errorCount}`)
    }
    if (warningCount > 0) {
      recommendations.push(` ${warningCount}`)
    }
  }

  const isValid = issues.filter(issue => issue.type === 'error').length === 0

  return {
    isValid,
    issues,
    recommendations
  }
}

/**
 * 
 */
export function generateConfigReport(result: ConfigValidationResult): string {
  let report = '## Supabase\n\n'

  if (result.isValid) {
    report += ' ****\n\n'
  } else {
    report += ' ****\n\n'
  }

  // 
  if (result.issues.length > 0) {
    report += '### \n'
    
    const errors = result.issues.filter(issue => issue.type === 'error')
    const warnings = result.issues.filter(issue => issue.type === 'warning')
    const infos = result.issues.filter(issue => issue.type === 'info')

    if (errors.length > 0) {
      report += '#### \n'
      errors.forEach(issue => {
        report += `-  ${issue.message}`
        if (issue.fix) report += `\n   : ${issue.fix}`
        report += '\n'
      })
      report += '\n'
    }

    if (warnings.length > 0) {
      report += '#### \n'
      warnings.forEach(issue => {
        report += `-  ${issue.message}`
        if (issue.fix) report += `\n   : ${issue.fix}`
        report += '\n'
      })
      report += '\n'
    }

    if (infos.length > 0) {
      report += '#### \n'
      infos.forEach(issue => {
        report += `-  ${issue.message}\n`
      })
      report += '\n'
    }
  }

  // 
  if (result.recommendations.length > 0) {
    report += '### \n'
    result.recommendations.forEach(rec => {
      report += `- ${rec}\n`
    })
  }

  // 
  report += '\n### \n'
  report += `- : ${import.meta.env.MODE}\n`
  report += `- Supabase URL: ${import.meta.env.VITE_SUPABASE_URL || ''}\n`
  report += `- Anon Key: ${import.meta.env.VITE_SUPABASE_ANON_KEY ? '' : ''}\n`

  return report
}

/**
 * 
 */
export async function logConfigReport(): Promise<void> {
  console.log(' Supabase...')
  
  const result = await validateSupabaseConfig()
  const report = generateConfigReport(result)
  
  console.log(report)
  
  if (result.isValid) {
    console.log(' Supabase')
  } else {
    console.warn(' Supabase')
  }
}

// 
if (import.meta.env.DEV) {
  setTimeout(() => {
    logConfigReport()
  }, 1000)
}